.main-button{
    font-size: 18px;
    line-height: 21px;
    color: #5E6C84;
    padding: 0;
    margin-top: 5px;
    text-align: left;
    border: none;
    width: 65%;
    cursor: pointer;
    border-radius: 15%;
}
.main-button:hover{
    background-color: #bdc2cb;
}
.main-button:disabled{
    cursor:auto;
    background: transparent;
    color: #c2c2c2;
}